/* App.css */

/* Basic container styles */
.app-container {
  text-align: center;
  font-family: 'Arial', sans-serif;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #f5f5f5;
}

/* Header styles */
.header {
  margin-bottom: 30px;
}

.title {
  font-size: 3rem;
  color: #2c3e50;
  font-weight: bold;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Search bar and button styles */
.search-form {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-input {
  padding: 12px;
  font-size: 1.2rem;
  border: 2px solid #ccc;
  border-radius: 5px;
  width: 280px;
  margin-right: 15px;
  outline: none;
  transition: border-color 0.3s ease;
}

.search-input:focus {
  border-color: #3498db;
}

.search-button {
  padding: 12px 25px;
  background-color: #3498db;
  color: white;
  font-size: 1.2rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.search-button:hover {
  background-color: #2980b9;
}

/* Suggestions list styles */
.suggestions-list {
  list-style: none;
  margin-top: 5px;
  padding-left: 0;
  max-height: 200px;
  overflow-y: auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: white;
  position: absolute;
  width: 100%;
  z-index: 1000;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.suggestions-list li {
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.suggestions-list li:hover {
  background-color: #f0f0f0;
}

/* Loading and error messages */
.loading,
.error {
  font-size: 1.2rem;
  margin-top: 20px;
}

.error {
  color: red;
  font-weight: bold;
}

/* Weather display styles */
.weather-display {
  margin-top: 40px;
  padding: 25px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #ffffff;
  display: inline-block;
  max-width: 400px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.weather-city {
  font-size: 2rem;
  font-weight: bold;
  color: #2c3e50;
}

.weather-info {
  font-size: 1.1rem;
  margin-top: 15px;
}

.weather-info p {
  margin: 8px 0;
}

/* Mobile responsiveness */
@media (max-width: 600px) {
  .search-input {
    width: 230px;
  }

  .search-button {
    width: 180px;
  }

  .weather-display {
    width: 90%;
    margin: 0 auto;
  }

  .title {
    font-size: 2.2rem;
  }
}
