.app-container {
  text-align: center;
  background-color: #f5f5f5;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.header {
  margin-bottom: 30px;
}

.title {
  color: #2c3e50;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 10px;
  font-size: 3rem;
  font-weight: bold;
}

.search-form {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.search-input {
  border: 2px solid #ccc;
  border-radius: 5px;
  outline: none;
  width: 280px;
  margin-right: 15px;
  padding: 12px;
  font-size: 1.2rem;
  transition: border-color .3s;
}

.search-input:focus {
  border-color: #3498db;
}

.search-button {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  padding: 12px 25px;
  font-size: 1.2rem;
  transition: background-color .3s;
}

.search-button:hover {
  background-color: #2980b9;
}

.suggestions-list {
  z-index: 1000;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  max-height: 200px;
  margin-top: 5px;
  padding-left: 0;
  list-style: none;
  position: absolute;
  overflow-y: auto;
  box-shadow: 0 2px 8px #0003;
}

.suggestions-list li {
  cursor: pointer;
  padding: 10px;
  transition: background-color .3s;
}

.suggestions-list li:hover {
  background-color: #f0f0f0;
}

.loading, .error {
  margin-top: 20px;
  font-size: 1.2rem;
}

.error {
  color: red;
  font-weight: bold;
}

.weather-display {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 10px;
  max-width: 400px;
  margin-top: 40px;
  padding: 25px;
  display: inline-block;
  box-shadow: 0 4px 10px #0000001a;
}

.weather-city {
  color: #2c3e50;
  font-size: 2rem;
  font-weight: bold;
}

.weather-info {
  margin-top: 15px;
  font-size: 1.1rem;
}

.weather-info p {
  margin: 8px 0;
}

@media (width <= 600px) {
  .search-input {
    width: 230px;
  }

  .search-button {
    width: 180px;
  }

  .weather-display {
    width: 90%;
    margin: 0 auto;
  }

  .title {
    font-size: 2.2rem;
  }
}
